import { GetComponentProps, useActions } from 'topwrite';
import { useEffect } from 'react';
import { HighlightPlugin } from '@/highlight-plugin';

export default function Activate({ children }: GetComponentProps<'activate'>) {
    const { addPlugin, removePlugin } = useActions('editor');
    //todo  新增usePlugin，简化操作
    useEffect(() => {
        addPlugin(HighlightPlugin);
        return () => {
            removePlugin(HighlightPlugin);
        };
    }, []);
    return children;
}
