import Prism from 'prismjs';
import 'prismjs/components/prism-markdown';

Prism.languages.insertBefore('markdown', 'blockquote', {
    'code-block': {
        pattern: /^```.*$/m,
        alias: 'code'
    },
    leafDirective: {
        pattern: /^\:\:(w+?)/,
        alias: 'code'
    }
});

Prism.languages.insertBefore('markdown', 'url-reference', {
    'image': {
        pattern: /!\[[^\]]*\](?:\([^)]+\))/,
        alias: 'url'
    },
});

Prism.languages.insertBefore('markdown', 'prolog', {
    'align': {
        pattern: /:?-+?:?/,
        alias: 'keyword'
    }
});

export default Prism;
