import Prism from './utils/prsim';
import { createPlatePlugin, css, styled, useEditorActions } from 'topwrite';
import { Token } from 'prismjs';
import { Path, Range, NodeEntry, CodeElement } from 'topwrite/plate';

const languages = {
    'application/json': 'javascript',
    'text/css': 'css',
    'text/markdown': 'markdown'
};

const getLength = (token: Token | string): number => {
    if (typeof token === 'string') {
        return token.length;
    } else {
        let content = token.content;
        if (typeof content === 'string') {
            return content.length;
        } else {
            if (!Array.isArray(content)) {
                content = [content];
            }
            return content.reduce((l, t) => l + getLength(t), 0);
        }
    }
};

export const HighlightPlugin = createPlatePlugin({
    renderLeaf() {
        return ({ leaf, children }) => {
            return <Container
                $comment={leaf.comment || leaf.prolog || leaf.doctype || leaf.cdata}
                $punctuation={leaf.punctuation}
                $namespace={leaf.namespace}
                $property={leaf.property || leaf.tag || leaf.boolean || leaf.number || leaf.constant || leaf.symbol || leaf.delete}
                $selector={leaf.selector || leaf['attr-name'] || leaf.string || leaf.char || leaf.builtin || leaf.inserted}
                $operator={leaf.operator || leaf.entity || leaf.url}
                $keyword={leaf.keyword || leaf.atrule || leaf['attr-value']}
                $function={leaf.function || leaf['class-name']}
                $variable={leaf.variable || leaf.regex || leaf.important}
                $important={leaf.important || leaf.bold}
                $italic={leaf.italic}
                $entity={leaf.entity}
            >
                {children}
            </Container>;
        };
    },
    decorate(editor) {
        const { Element, Text, Node } = useEditorActions();
        if (editor.isDocument) {
            return ([node, path]: NodeEntry) => {
                const ranges: Range[] = [];
                if (Element.isElementType<CodeElement>(node, 'code')) {
                    const lang = node.lang || 'clike';
                    const grammar = Prism.languages[lang] || Prism.languages['clike'];

                    let texts = Array.from(Node.texts(node));
                    const nodeText = texts.map(([t]) => t.text).join('\n');
                    const tokens = Prism.tokenize(nodeText, grammar);

                    let textStart = 0;
                    let textEnd = 0;

                    texts.forEach(([text, p]) => {
                        textEnd = textStart + text.text.length;

                        let offset = 0;

                        function processToken(token: Token | string, marks: string[] = []) {
                            if (typeof token === 'string') {
                                if (marks.length > 0) {
                                    const range = createRange({
                                        path: path.concat(p),
                                        textStart,
                                        textEnd,
                                        start: offset,
                                        end: offset + token.length,
                                        marks: marks
                                    });
                                    if (range) {
                                        ranges.push(range);
                                    }
                                }
                                offset += token.length;
                            } else {
                                marks.push(token.type);
                                if (token.alias) {
                                    marks.push(...Array.isArray(token.alias) ? token.alias : [token.alias]);
                                }
                                let content = token.content;
                                if (typeof content === 'string') {
                                    const range = createRange({
                                        path: path.concat(p),
                                        textStart,
                                        textEnd,
                                        start: offset,
                                        end: offset + content.length,
                                        marks: marks
                                    });
                                    if (range) {
                                        ranges.push(range);
                                    }

                                    offset += content.length;
                                } else {
                                    if (!Array.isArray(content)) {
                                        content = [content];
                                    }
                                    // When using token.content instead of token.matchedStr, token can be deep
                                    for (let i = 0; i < content.length; i += 1) {
                                        processToken(content[i], marks);
                                    }
                                }
                            }
                        }

                        tokens.forEach(token => processToken(token));
                        textStart = textEnd + 1; // account for added `\n`
                    });
                }
                return ranges;
            };
        } else {
            const language = languages[editor.mime as keyof typeof languages];
            if (!language) {
                return;
            }
            return ([node, path]) => {
                if (!Text.isText(node)) {
                    return [];
                }

                const ranges: Range[] = [];
                const tokens = Prism.tokenize(node.text, Prism.languages[language]);
                let start = 0;

                for (const token of tokens) {
                    const length = getLength(token);
                    const end = start + length;

                    if (typeof token !== 'string') {
                        const range = {
                            [token.type]: true,
                            anchor: { path, offset: start },
                            focus: { path, offset: end },
                        };

                        if (token.alias) {
                            const alias = Array.isArray(token.alias) ? token.alias : [token.alias];
                            for (const name of alias) {
                                range[name] = true;
                            }
                        }

                        ranges.push(range);
                    }

                    start = end;
                }

                return ranges;
            };
        }
    }
});

function createRange({
    path,
    textStart,
    textEnd,
    start,
    end,
    marks
}: {
    path: Path,
    textStart: number,
    textEnd: number,
    start: number,
    end: number,
    marks: string[]
}): Range | null {
    if (start >= textEnd || end <= textStart) {
        // Ignore, the token is not in the text
        return null;
    }

    // Shrink to this text boundaries
    start = Math.max(start, textStart);
    end = Math.min(end, textEnd);

    // Now shift offsets to be relative to this text
    start -= textStart;
    end -= textStart;

    const range: Range = {
        anchor: {
            path,
            offset: start,
        },
        focus: {
            path,
            offset: end
        }
    };

    for (const mark of marks) {
        // @ts-ignore
        range[mark] = true;
    }

    return range;
}


interface Style {
    $comment?: boolean;
    $punctuation?: boolean;
    $namespace?: boolean;
    $property?: boolean;
    $selector?: boolean;
    $operator?: boolean;
    $keyword?: boolean;
    $function?: boolean;
    $variable?: boolean;
    $important?: boolean;
    $italic?: boolean;
    $entity?: boolean;
}

const Container = styled.span<Style>`
    ${props => props.$comment && css`
        color: slategray;
    `}
    ${props => props.$punctuation && css`
        color: #999;
    `}
    ${props => props.$namespace && css`
        opacity: .7;
    `}
    ${props => props.$property && css`
        color: #905;
    `}
    ${props => props.$selector && css`
        color: #690;
    `}
    ${props => props.$operator && css`
        color: #9a6e3a;
    `}
    ${props => props.$keyword && css`
        color: #07a;
    `}
    ${props => props.$function && css`
        color: #DD4A68;
    `}
    ${props => props.$variable && css`
        color: #e90;
    `}
    ${props => props.$important && css`
        font-weight: bold;
    `}
    ${props => props.$italic && css`
        font-style: italic;
    `}
    ${props => props.$entity && css`
        cursor: help;
    `}
`;
